'use client';

import { useEffect, useState } from 'react';
import { useUser } from '@/src/context/AuthContext';
import { RentalCompany } from '@/src/lib/rentalCompanies/rentalCompanyTypes';
import { getMyRentalCompany, updateRentalCompany } from '@/src/components/rental-management/rental-companies/rentalCompanyService';
import RentalCompanyForm from '@/src/components/rental-management/rental-companies/CompanyForm';
import { useRouter } from 'next/navigation';
import { toast } from 'sonner';

export default function RentalCompanyPage() {
  const { user } = useUser();
  const router = useRouter();
  const [company, setCompany] = useState<RentalCompany | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchCompany = async () => {
      if (!user) return;
      const data = await getMyRentalCompany(user.uid);
      if (!data) {
        toast.error('No rental company found for this user');
        router.push('/my-business/create');
      } else {
        setCompany(data);
        setLoading(false);
      }
    };

    fetchCompany();
  }, [user, router]);

  const handleSave = async (data: Omit<RentalCompany, 'id'>) => {
    if (!user || !company) return;
    try {
      await updateRentalCompany(company.id, data);
      toast.success('Company updated successfully');
    } catch (err) {
      toast.error('Failed to update company');
      console.error(err);
    }
  };

  if (loading) return <div className="text-center py-10">Loading...</div>;

  return (
    <div className="max-w-2xl mx-auto py-6">
      <h1 className="text-xl font-bold mb-4">My Rental Company</h1>
      <div className="bg-white rounded-xl shadow p-4">
        <RentalCompanyForm
          editingCompany={company}
          onSave={handleSave}
          onCancel={() => router.push('/my-business/dashboard')}
        />
      </div>
    </div>
  );
}

